<template>
  <div class="accountManage">
    <div class="record-bar">
      <van-nav-bar :title="$t('UserInfo')" left-arrow @click-left="linkHome" />
    </div>
    <div class="tool">
      <van-cell :title="$t('account')" :value="account" center>
        <div slot="default" class="accountClass" v-if="this.disc === 'fictitious'">
          <div class="accountClass-logo">模拟</div>
          <div>{{ account }}</div>
        </div>
      </van-cell>
      <!--<van-cell title="手机号" is-link :value="phone" center @click="linkBindPhone"/>-->
      <van-cell :title="$t('RealName')" is-link :value="realNameState" :value-class="realNameState === $t('yRealName') ? 'not-value-class' : 'value-class'" 
        center @click="linkCertification" v-if="this.disc !== 'fictitious'">
      </van-cell>
      <van-cell :title="$t('Bankcard')" is-link center class="hImg-box" v-if="this.disc !== 'fictitious'" @click="linkAuthentication">
        <div slot="default" v-if="realNameBackState" style="display: flex; align-items: center">
          <div style="margin-left: auto">{{ bank }}</div>
        </div>
        <div slot="default" v-else>
          <div style="width: 100%; color: #ec4138">{{ $t("Unnamed") }}</div>
        </div>
      </van-cell>
    </div>
    <!--    <div class="password tool" v-if="this.disc !== 'fictitious'">-->
    <!--      <van-cell title="修改登录密码" is-link center @click="linkChangePassword"/>-->
    <!--      <van-cell title="修改资金密码" is-link center @click="linkModifyFundPass"/>-->
    <!--    </div>-->
    <!--    <div class="password tool" >-->
    <!--      <van-cell title="检测新版本" is-link center @click="inspect"/>-->
    <!--    </div>-->
    <!--    <div class="my-but" @click="linkLogout">退出登录</div>-->
  </div>
</template>

<script>
import EP from "../../../utils/encryption";
import { Toast } from "vant";
// plus.navigator.setStatusBarStyle('dark');
export default {
  name: "myNews",
  data() {
    return {
      // 盘
      disc: "",
      // 账号
      account: "",
      // 手机号
      phone: "",
      // 实名状态
      realNameState: "",
      realNameBackState: "",
      // 银行
      bank: "",
      // 银行图标
      bankIcon: "",
      // 银行号码
      bankAccount: "",
      // 审核状态
      state: "",
      // 有无设置资金密码
      isDrawPass: "",
    };
  },
  created() {
    this.state = localStorage.getItem("status");
    this.isDrawPass = localStorage.getItem("isDrawPass");
    // nRealName:'not certified',
    //   dRealName:'wait Review',
    //   yRealName:'verified',
    if (this.state === "0") {
      this.realNameState = this.$t("nRealName");
      this.realNameBackState = false;
    } else if (this.state === "3") {
      this.realNameState = this.$t("dRealName");
      this.realNameBackState = false;
    } else if (this.state === "1") {
      this.realNameState = this.$t("yRealName");
      this.realNameBackState = true;
      this.bank = localStorage.getItem("bank");
      // this.bankIcon = require('../../../assets/bank/'+this.bank+'.png')
      // let cbank = EP.decrypt(localStorage.getItem('bankAccount'),'')
      // this.bankAccount = cbank.substr(cbank.length-4)
    }
    this.disc = localStorage.getItem("environment");
    this.account = localStorage.getItem("account");
    this.phone =
      localStorage.getItem("phone").substr(0, 3) +
      "*****" +
      localStorage.getItem("phone").substr(8);
  },
  methods: {
    // 返回上级
    linkHome() {
      this.$router.push({
        name: "my",
      });
    },
    inspect() {
      console.log("触发");
      this.$emit("upData", "121212");
    },
    // 跳转到实名认证
    linkCertification() {
      let that = this;
      console.log(this.realNameState);
      if (this.realNameState === this.$t("nRealName")) {
        this.$router.push({
          name: "register",
        });
      } else if (this.realNameState === this.$t("dRealName")) {
        this.$router.push({
          name: "toBeAudited",
        });
      } else {
        this.$router.push({
          name: "certification",
        });
      }
    },
    // 跳转到银行卡安全认证
    linkAuthentication() {
      let that = this;
      if (this.isDrawPass != 1) {
        Toast({
          position: "bottom",
          message: that.$t("noUnnamed"),
          duration: 1000,
        });
      } else {
        this.$router.push({
          name: "authentication",
          params: {
            form: "accountManage",
          },
        });
      }
    },
    // 跳转到修改登录密码
    linkChangePassword() {
      this.$router.push({
        name: "myChangePassword",
      });
    },
    // 跳转到修改资金密码
    linkModifyFundPass() {
      let that = this;
      console.log(this.isDrawPass);
      if (this.isDrawPass === "1") {
        this.$router.push({
          name: "modifyFundPass",
          params: {
            modify: "accountManage",
          },
        });
      } else {
        Toast({
          position: "bottom",
          message: that.$t("noUnnamed"),
          duration: 1000,
        });
      }
    },
    // 绑定手机号码
    linkBindPhone() {
      this.$router.push({
        name: "bindPhone",
      });
    },
    // 退出登录
    linkLogout() {
      this.Logout();
    },
  },
};
</script>

<style scoped lang="scss">
@import "../../../style/my";
.value-class {
  color: #ec4138 !important;
}
.not-value-class {
  color: #222 !important;
}
</style>



